/*
// Examples Stylesheet
// -------------------
*/

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #333;
  margin: 0;
  padding: 0;
}

a {
  color: #08c;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 720px;
  padding: 1em;
}

.footer {
  margin-top: 50px;
  border-top: 1px solid #eee;
  padding: 20px 0;
  font-size: 12px;
  color: #999;
}

h1, h2, h3, h4, h5, h6 {
  color: #222;
  font-weight: 100;
  margin: 0.5em 0;
}

label {
  color: #999;
  display: inline-block;
  font-size: 0.85em;
  font-weight: bold;
  margin: 1em 0;
  text-transform: uppercase;
}

.hint {
  margin: 15px 0;
  font-style: italic;
  color: #999;
}

.occamy-text-example {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;

  margin: 30px 0;
}

.box {
  width: 100%;
  height: 240px;
  max-width: 180px;

  padding: 20px;
  border: 1px solid #777;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;

  color: #555;
}



